    flex-wrap: wrap;
    justify-content: flex-start;
    width: 100%;
    margin-left: -15px;
}

.tab {
    position: relative;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0 15px;
    margin-bottom: 10px;
    line-height: 30px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: .5s;
    transition-property: color;
}

.tab::after {
    content: "";
    display: block;
    height: 2.5px;
    max-width: 60px;
    width: 100%;
    transition: .5s;
    background-color: white;
}

.active-tab::after {
    content: "";
    display: block;
    background-color: #ef224b;
    height: 2.5px;
    max-width: 190px;
    width: 100%;
    transition: .5s
}

